<template>
  <Layout id="map">
    <TalIframe :content-src="dataSrc" />
  </Layout>
</template>

<script>
import { generateAuditPersonFinanceList, auditMissingReportTip } from '@/api/reimbursement'
import TalIframe from '@/components/iframe'
import { mapMutations } from 'vuex'
export default {
  name: 'Map',
  data () {
    return {
      dataSrc: '',
      cityCode: null
    }
  },
  components: { TalIframe },
  mounted () {
    this.auditMissingReportTip()
  },
  methods: {
    ...mapMutations([
      'closeTag'
    ]),
    generateAuditPersonFinanceList () {
      generateAuditPersonFinanceList({
        cityCode: this.cityCode
      }).then(data => {
        this.dataSrc = data.url
      }).catch(err => {
        this.$Message.error({
          content: err.message,
          duration: 5
        })
      })
    },
    auditMissingReportTip () { // 判断用户权限
      auditMissingReportTip({
        isAudit: 2 // 查询无需校验是否补报
      }).then(res => {
        this.cityCode = res
        this.generateAuditPersonFinanceList()
      }).catch((err) => {
        if (err.code === '40004') {
          this.$Modal.info({
            title: '提示',
            content: err.message,
            okText: '去补报',
            onOk: () => {
              let route = this.$route
              this.closeTag(route)
              this.$router.push({
                name: 'prescribeddaily'
              })
            }
          })
        } else {
          this.$Modal.info({
            title: '提示',
            content: err.message,
            okText: '确定',
            onOk: () => {
              let route = this.$route
              this.closeTag(route)
            }
          })
        }
      })
    }
  }
}
</script>
