<script>
import { dataChangeDay } from '@/utils/utils'
export default {
  name: 'reimbursement',
  props: [
    'dialogShow',
    'detailList'
  ],
  data () {
    return {
      day: '',
      tableTotalData: []
    }
  },
  watch: {
    'detailList.queryDate' () {
      this.day = dataChangeDay(this.detailList.queryDate)
    },
    dialogShow () {
      if (this.dialogShow) {
        this.getTotalData()
      }
    }
  },
  filters: {
    filterType (type) {
      if (type === 1) {
        return '营业款'
      } else if (type === 2) {
        return '临时备用金'
      } else if (type === 3) {
        return '长期备用金'
      } else if (type === 4) {
        return '其他收支'
      } else {
        return '--'
      }
    }
  },
  mounted () {
    this.getTotalData()
  },
  methods: {
    cancel (flag) {
      if (flag === false) {
        this.$emit('changeDialog', false)
      }
    },
    getTotalData () {
      // 获取数据之后进行处理
      let obj1 = {
        operateName: '合计'
      }
      let obj2 = {
        operateName: '款项所属服务中心合计'
      }
      let obj3 = {
        operateName: '合计差额'
      }
      if (this.detailList.operateLists && this.detailList.operateLists.length > 0) {
        this.detailList.operateLists.forEach((val, key) => {
          if (val.operateId === '999999') {
            // 款项所属服务中心合计为后台返回
            obj2 = Object.assign(val, obj2)
          } else {
            // 累加合计数据
            for (var i in val) {
              obj1[i] = obj1[i] ? obj1[i] : 0
              if (i !== 'operateName') {
                obj1[i] += val[i]
              }
            }
          }
        })
        // 合计差额为款项所属服务中心合计-合计
        for (var i in obj1) {
          if (i !== 'operateName') {
            obj3[i] = (obj2[i] ? obj2[i] : 0) - (obj1[i] ? obj1[i] : 0)
          }
        }
        obj2.receiptAmount = obj3.receiptAmount = null // 实际现金小结合计差额为--
        obj2.posReceiptReceiveAmount = obj3.posReceiptReceiveAmount = null // 实际现金小结合计差额为--
        obj2.posReceiptRefundAmount = obj3.posReceiptRefundAmount = null // 实际现金小结合计差额为--
        this.tableTotalData = [obj1, obj2, obj3]
      }
    }
  }
}
</script>

