<template>
  <div class="reimbursement">
    <div class="pres-top">
      <span class="firspan">款项所属服务中心：{{userMessage.serviceCenterName}}</span>
      <span>报账日期：</span>
      <DatePicker v-model="dayAuditId" type="date" format="yyyy-MM-dd" placement="bottom-end" style="width: 200px" placeholder="请选择日期" @on-change="timeChange"></DatePicker>
      <Button type="primary" @click="auditDayList" :loading="tableLoading">查询</Button>
    </div>
    <div class="pres-center">
      <Table border :columns="columns" :data="tableData" :loading="tableLoading"></Table>
    </div>
    <centerview :dialogShow="dialogShow" @changeDialog="changeDialog" :detailList="detailList"></centerview>
    <promptdialog :dialogShow="supplementShow" :dialogText="supplementText" @changeDialog="supplementDialog"></promptdialog>
  </div>
</template>

<script>
import {
  // serviceDailyAuditInfo,
  auditServiceDailyInfo,
  serviceCenterEmpInfo,
  auditMissingReportTip,
  auditDayList
} from '@/api/reimbursement'
// import {
//   getCityList
// } from '@/api/operative'
import centerview from './centerview'
import promptdialog from '@/components/financial/promptdialog'
import { mapMutations } from 'vuex'
export default {
  name: 'reimbursementpreview',
  data () {
    return {
      supplementShow: false,
      supplementText: {
        content: '此服务中心存在漏报账，请及时补报！',
        button: '去补报'
      },
      dayAuditId: '',
      tableLoading: false,
      columns: [
        {
          title: '操作',
          key: 'action',
          width: 80,
          align: 'center',
          render: (h, params) => {
            return h('div', [
              h('span', {
                style: {
                  marginRight: '5px',
                  color: 'blue',
                  cursor: 'pointer'
                },
                on: {
                  click: () => {
                    this.show(params.row.id)
                  }
                }
              }, '查看')
            ])
          }
        },
        {
          title: '服务中心',
          key: 'serviceCenterName',
          align: 'center'
        },
        {
          title: '报账人',
          key: 'createName',
          align: 'center'
        },
        {
          title: '报账日期',
          key: 'auditDate',
          align: 'center'
        },
        {
          title: '操作时间',
          key: 'createTime',
          align: 'center'
        },
        {
          title: '当前现金实际金额合计',
          key: 'actualAccountAmount',
          align: 'center'
        },
        {
          title: '审核状态',
          key: 'auditStauts',
          align: 'center',
          render: (h, params) => {
            let text = ''
            if (params.row.auditStauts === 0) {
              text = '未审核'
            } else if (params.row.auditStauts === 1) {
              text = '审核通过'
            } else if (params.row.auditStauts === 2) {
              text = '审核不通过'
            }
            return h('span', {
            }, text)
          }
        }
      ],
      tableData: [],
      dialogShow: false,
      detailList: {
        operateList: [],
        servcenterDepositList: [],
        servcenterOtherBudget: {},
        serventerDayAudit: {},
        remarks: '',
        userMessage: {
          serviceCenterName: '',
          empName: '',
          nowDate: ''
        }
      },
      cityCode: '',
      userMessage: {
        serviceCenterName: ''
      }
    }
  },
  components: {
    centerview,
    promptdialog
  },
  beforeMount () {
    this.auditMissingReportTip()
  },
  methods: {
    ...mapMutations([
      'closeTag'
    ]),
    auditMissingReportTip () { // 判断用户权限
      auditMissingReportTip({
        isAudit: 2 // 查询无需校验是否补报
      }).then(res => {
        this.cityCode = res
        this.serviceCenterEmpInfo()
      }).catch((err) => {
        if (err.code === '40004') {
          this.$Modal.info({
            title: '提示',
            content: err.message,
            okText: '去补报',
            onOk: () => {
              let route = this.$route
              this.closeTag(route)
              this.$router.push({
                name: 'prescribeddaily'
              })
            }
          })
        } else {
          this.$Modal.info({
            title: '提示',
            content: err.message,
            okText: '确定',
            onOk: () => {
              let route = this.$route
              this.closeTag(route)
            }
          })
        }
      })
    },
    timeChange (time) {
      this.dayAuditId = time
    },
    supplementDialog (flag) { // 有漏报时进行补报
      if (flag === true) {

      } else {
        this.supplementShow = false
      }
    },
    show (id) {
      this.auditServiceDailyInfo(id)
    },
    changeDialog (flag) {
      if (flag === true) {

      } else {
        this.dialogShow = false
      }
    },
    auditServiceDailyInfo (id) { // 查看获取明细信息
      auditServiceDailyInfo({
        dayAuditId: id,
        cityCode: this.cityCode,
        queryType: 1
      }).then((data) => {
        this.detailList = data
        this.detailList.userMessage = this.userMessage
        this.dialogShow = true
      }).catch((err) => {
        this.$Message.warning({ content: '获取明细信息失败:' + err.message, duration: 5 })
      })
    },
    serviceCenterEmpInfo () { // 获取用户信息
      serviceCenterEmpInfo({ cityCode: this.cityCode }).then((data) => {
        this.userMessage = data
      }).catch((err) => {
        this.$Message.warning({ content: '获取服务中心用户信息失败:' + err.message, duration: 5 })
      })
    },
    auditDayList () { // 获取列表信息
      this.tableLoading = true
      auditDayList({ cityCode: this.cityCode, startAuditDate: this.dayAuditId }).then((data) => {
        if (data instanceof Array) {
          this.tableData = data
        } else {
          this.$Message.warning({ content: '获取列表信息失败:' + data.message, duration: 5 })
        }
      }).catch((err) => {
        this.$Message.warning({ content: '获取列表信息失败:' + err.message, duration: 5 })
      }).finally(() => {
        this.tableLoading = false
      })
    }
  }
}
</script>

