  .reimbursement {
    background: #eee;
    font-size: 14px;
    padding: 30px 20px 0;
    width: 100%;
    overflow-y: auto;
    .pres-top{
      background: #fff;
      padding:20px;
      span{
        padding:0 20px;
      }
      .firspan{
        padding-left:0;
      }
      .ivu-btn{
        margin-left:20px;
      }
    }
  }
  .pres-center{
    padding:20px;
    background: #fff;
    margin-top:20px;
  }
  .pres-bottom{
    background: #fff;
    padding-bottom:20px;
    display: flex;
    justify-content: center;
    font-size: 12px;
  }
</style>
