<template>
    <div class="prescribeddaily">
      <div class="pres-top">
        <span class="firspan">漏报日期</span>
        <DatePicker :value="dataRange" format="yyyy-MM-dd" type="daterange" placement="bottom-end" separator="至" placeholder="请选择日期" @on-change="timeChange" @on-clear="clearChange" transfer></DatePicker>
        <span>补报状态</span>
        <Select v-model="form.commitStatus" style="width:100px" transfer>
          <Option  value='all' >全部</Option>
          <Option  value=1 >已补报</Option>
          <Option  value=0 >未补报</Option>
        </Select>
        <Button type="primary" @click="auditMissingReports(1)" :loading="tableLoading">查询</Button>
      </div>
      <div class="pres-center">
        <Table border :columns="columns7" :data="tableData" :loading="tableLoading"></Table>
      </div>
      <div class="pres-bottom">
        <Page :total="total" show-sizer  show-elevator show-total @on-change="changePageNum" @on-page-size-change="changePageSize" :current="form.pageNo"/>
      </div>
      <centerview :dialogShow="dialogShow" @changeDialog="changeDialog" :detailList="detailList"></centerview>
      <promptdialog :dialogShow="supplementShow" :dialogText="supplementText" @changeDialog="supplementDialog"></promptdialog>
    </div>
</template>

<script>
import promptdialog from '@/components/financial/promptdialog'
import {
  auditMissingReports,
  auditServiceDailyInfo,
  auditMissingReportTip
} from '@/api/reimbursement'
import centerview from './centerview'
import { mapMutations } from 'vuex'
export default {
  name: 'prescribeddaily',
  data () {
    return {
      form: {
        startMissingTime: '',
        endMissingTime: '',
        commitStatus: '',
        pageSize: 10,
        pageNo: 1
      },
      total: 0,
      tableLoading: false,
      columns7: [
        {
          title: '操作',
          key: 'action',
          width: 80,
          align: 'center',
          render: (h, params) => {
            let text = ''
            if (params.row.commitStatus === 0) {
              text = '补报'
            } else if (params.row.commitStatus === 1) {
              text = '查看'
            }
            return h('div', [
              h('span', {
                style: {
                  marginRight: '5px',
                  color: 'blue',
                  cursor: 'pointer'
                },
                on: {
                  click: () => {
                    if (params.row.commitStatus === 1) {
                      this.auditServiceDailyInfo(params.row.id)
                    } else {
                      this.$router.push({ name: `reimbursementForMulti`,
                        params: { id: new Date().getTime() },
                        query: {
                          type: 'accountType',
                          time: params.row.missTime
                        } })
                    }
                  }
                }
              }, text)
            ])
          }
        },
        {
          title: '漏报日期',
          key: 'missTime',
          align: 'center'
        },
        {
          title: '是否有缴退费',
          key: 'changeStatus',
          align: 'center',
          render: (h, params) => {
            let text = ''
            if (params.row.changeStatus === 0) {
              text = '否'
            } else if (params.row.changeStatus === 1) {
              text = '是'
            }
            return h('span', {
            }, text)
          }
        },
        {
          title: '是否已补报',
          key: 'commitStatus',
          align: 'center',
          render: (h, params) => {
            let text = ''
            if (params.row.commitStatus === 0) {
              text = '否'
            } else if (params.row.commitStatus === 1) {
              text = '是'
            }
            return h('span', {
            }, text)
          }
        },
        {
          title: '补报人',
          key: 'commitName',
          align: 'center'
        },
        {
          title: '操作时间',
          key: 'commitTime',
          align: 'center'
        },
        {
          title: '补报原因',
          key: 'reason',
          align: 'center'
        }
      ],
      tableData: [],
      dialogShow: false,
      dataRange: [],
      detailList: {
        operateLists: [],
        personDayAuditList: [],
        servcenterDepositList: [],
        servcenterOtherBudget: {},
        serventerDayAudit: {},
        tableTotalData: []
      },
      cityCode: '',
      supplementShow: false,
      supplementText: {
        content: '此服务中心存在漏报账，请及时补报！',
        button: '去补报'
      },
      nowRage: []
    }
  },
  components: {
    centerview,
    promptdialog
  },
  beforeMount () { // 默认显示最近30天的数据
    let date = new Date()
    let year = date.getFullYear()
    let month = date.getMonth() + 1
    let tdate = date.getDate()
    let today = year + '-' + (month < 10 ? '0' + month : month) + '-' + (tdate < 10 ? '0' + tdate : tdate)
    let newDay = new Date(year, date.getMonth(), (tdate - 31))
    let pyear = newDay.getFullYear()
    let pmonth = newDay.getMonth() + 1
    let ptdate = newDay.getDate()
    let ptoday = pyear + '-' + (pmonth < 10 ? '0' + pmonth : pmonth) + '-' + (ptdate < 10 ? '0' + ptdate : ptdate)
    this.form.startMissingTime = ptoday
    this.form.endMissingTime = today
    this.dataRange = [ptoday, today]
    this.nowRage = [ptoday, today]
    this.auditMissingReportTip()
  },
  methods: {
    ...mapMutations([
      'closeTag'
    ]),
    supplementDialog (flag) { // 有漏报时进行补报
      if (flag === true) {

      } else {
        this.supplementShow = false
      }
    },
    show (index) {
      this.$Modal.info({
        title: 'User Info',
        content: `Name：${this.data6[index].name}<br>Age：${this.data6[index].age}<br>Address：${this.data6[index].address}`
      })
    },
    changeDialog (flag) {
      if (flag === true) {

      } else {
        this.dialogShow = false
      }
    },
    auditServiceDailyInfo (id) { // 查看获取明细信息
      auditServiceDailyInfo({
        dayAuditId: id,
        cityCode: this.cityCode,
        queryType: 0
      }).then((data) => {
        if (!data) return
        this.detailList = data
        this.dialogShow = true
      }).catch((err) => {
        this.$Message.warning({ content: '获取服务中心用户信息失败:' + err.message, duration: 5 })
      })
    },
    auditMissingReports (type) { // 查询
      if (this.dataRange.length === 0) {
        this.dataRange = this.nowRage
        this.form.startMissingTime = this.dataRange[0]
        this.form.endMissingTime = this.dataRange[1]
      }
      if (type) {
        this.tableLoading = true
        this.form.pageNo = 1
      }
      let params = {
        ...this.form,
        cityCode: this.cityCode
      }
      if (params.commitStatus === 'all') {
        params.commitStatus = ''
      }
      auditMissingReports(params).then((data) => {
        this.tableData = data.records
        this.total = data.totalCount
      }).catch((err) => {
        this.$Message.warning({ content: '获取信息失败:' + err.message, duration: 5 })
      }).finally(() => {
        this.tableLoading = false
      })
    },
    timeChange (times) {
      if (times && times.length === 2) {
        const days = this.DateMinus(times[0], times[1])
        if (days > 30) {
          this.$Message.warning({ content: '时间跨度不能大于1个月', duration: 5 })
          this.dataRange = []
          this.form.startMissingTime = ''
          this.form.endMissingTime = ''
        } else {
          this.form.startMissingTime = times[0]
          this.form.endMissingTime = times[1]
        }
      }
    },
    clearChange () {
      this.dataRange = []
      this.form.startMissingTime = ''
      this.form.endMissingTime = ''
    },
    DateMinus (date1, date2) {
      const start = new Date(date1)
      const end = new Date(date2)
      const days = end.getTime() - start.getTime()
      const day = parseInt(days / (1000 * 60 * 60 * 24))
      return day
    },
    changePageNum (val) {
      this.form.pageNo = val
      this.auditMissingReports()
    },
    changePageSize (val) {
      this.form.pageSize = val
      this.auditMissingReports()
    },
    auditMissingReportTip () {
      auditMissingReportTip({
        isAudit: 2
      }).then(res => {
        this.cityCode = res
      }).catch((err) => {
        this.$Modal.info({
          title: '提示',
          content: err.message,
          okText: '确定',
          onOk: () => {
            let route = this.$route
            this.closeTag(route)
          }
        })
      })
    }
  }
}
</script>

