<template>
    <div class='personalcheck'>
      <Spin size="large" fix v-if="spinShow"></Spin>
      <div class="personalcheck-title ivu-common-shadow">
        <div>
          <span>服务中心：{{userMessage.serviceCenterName}}</span>
          <span>报账人：{{userMessage.empName}}</span>
          <span>{{userMessage.nowDate}} {{day}}</span>
        </div>
        <div v-show="$haveAuthority('roleRelativeData', '/SPLAT/auditFinance/switchQueryBtn')">
          <i-switch v-model="onOff" :before-change="handleBeforeChange"></i-switch>
          <Tooltip :maxWidth="200" transfer placement="left">
            <div slot="content">
              报账开关控制服务中心是否需要报账。打开开关，则服务中心正常营业需要报账；关闭开关，服务中心不需要操作报账，也无法进行收退费。
            </div>
            <Icon type="ios-help-circle-outline" :size="16"/>
          </Tooltip>
        </div>

      </div>
      <div class="panel-container ivu-common-shadow">
        <table>
          <tr>
            <th class="table-th pw80" rowspan="2">序号</th>
            <th class="table-th  pw100" rowspan="2">结账时间</th>
            <th colspan="2" class="table-th">营业款收费</th>
            <th colspan="2" class="table-th">营业款退费</th>
            <th colspan="2" class="table-th">今日现金结账</th>
            <th colspan="4" class="table-th">今日脱机POS结账</th>
          </tr>
          <tr>
            <td class="table-th  pw80">现金</td>
            <td class="table-th  pw80">脱机POS</td>
            <td class="table-th  pw80">现金</td>
            <td class="table-th  pw80">脱机POS</td>
            <td class="table-th pd6">营业额现金小结</td>
            <td class="table-th">实际现金小结</td>
            <td class="table-th pd6">营业收款小结</td>
            <td class="table-th">实际收款小票合计</td>
            <td class="table-th pd6">营业退款小结</td>
            <td class="table-th">实际退款小票合计</td>
          </tr>
          <tr class="person-bottom" v-for="(item, index) in tableList" :key="index" :class="'person-bg'+index">
            <td>{{index === tableList.length-1 ? '当前结账' : '结账'+(index+1)}}</td>
            <td>{{item.createTime}}</td>
            <td>{{item.receiveAmount|formatMoneyFilter}}</td>
            <td>{{item.posReceiveAmount|formatMoneyFilter}}</td>
            <td>{{item.refundAmount|formatMoneyFilter}}</td>
            <td>{{item.posRefundAmount|formatMoneyFilter}}</td>
            <td>{{item.confirmAmount|formatMoneyFilter}}</td>
            <td class="prem-input">
              <input type="number" placeholder="0.00" v-model="item.receiptAmount" step="0.01" @keyup="item.receiptAmount=item.receiptAmount.toString().match(/^(\-|\+)?\d+(\.\d{0,2})?/) ? item.receiptAmount.toString().match(/^(\-|\+)?\d+(\.\d{0,2})?/)[0] : ''" @input="changeData('receiptAmount')" @mousewheel.prevent>
            </td>
            <td>{{item.posReceiveAmount|formatMoneyFilter}}</td>
            <td class="prem-input">
              <input type="number" placeholder="0.00" v-model="item.posReceiptReceiveAmount" step="0.01" @keyup="item.posReceiptReceiveAmount=item.posReceiptReceiveAmount.match(/^(\-|\+)?\d+(\.\d{0,2})?/) ? item.posReceiptReceiveAmount.match(/^(\-|\+)?\d+(\.\d{0,2})?/)[0] : ''" @input="changeData('posReceiptReceiveAmount')" @mousewheel.prevent>
            </td>
            <td>{{item.posRefundAmount|formatMoneyFilter}}</td>
            <td class="prem-input">
              <input type="number" placeholder="0.00" v-model="item.posReceiptRefundAmount" step="0.01" @keyup="item.posReceiptRefundAmount=item.posReceiptRefundAmount.match(/^(\-|\+)?\d+(\.\d{0,2})?/) ? item.posReceiptRefundAmount.match(/^(\-|\+)?\d+(\.\d{0,2})?/)[0] : ''" @input="changeData('posReceiptRefundAmount')" @mousewheel.prevent>
            </td>
          </tr>
          <tr  class="person-bottom">
            <td>合计</td>
            <td>--</td>
            <td>{{totalData.receiveAmountAdd|formatMoneyFilter}}</td>
            <td>{{totalData.posReceiveAmountAdd|formatMoneyFilter}}</td>
            <td>{{totalData.refundAmountAdd|formatMoneyFilter}}</td>
            <td>{{totalData.posRefundAmountAdd|formatMoneyFilter}}</td>
            <td :class="{'color-red': flagOne}">{{totalData.confirmAmountAdd|formatMoneyFilter}}</td>
            <td :class="{'color-red': flagOne}">{{totalData.receiptAmountAdd|formatMoneyFilter}}</td>
            <td :class="{'color-red': flagTwo}">{{totalData.posReceiveAmountAdd|formatMoneyFilter}}</td>
            <td :class="{'color-red': flagTwo}">{{totalData.posReceiptReceiveAmountAdd|formatMoneyFilter}}</td>
            <td :class="{'color-red': flagThree}">{{totalData.posRefundAmountAdd|formatMoneyFilter}}</td>
            <td :class="{'color-red': flagThree}">{{totalData.posReceiptRefundAmountAdd|formatMoneyFilter}}</td>
          </tr>
        </table>
        <div class="note">
          <span>备注：</span>
          <Input v-model="remark" type="textarea" :autosize="{minRows: 3,maxRows: 5}" :maxlength=500></Input>
        </div>
        <div class="invoicing">
          <Button type="primary" @click="getMoney" :disabled="ifPay">结账</Button>
        </div>
      </div>
      <promptdialog :dialogShow="dialogShow" :dialogText="dialogText" @changeDialog="changeDialog"></promptdialog>
      <promptdialog :dialogShow="supplementShow" :dialogText="supplementText" @changeDialog="supplementDialog"></promptdialog>
      <promptdialog :dialogShow="sureShow" :dialogText="sureShowText" @changeDialog="sureDialog"></promptdialog>
    </div>
</template>

<script>
import { mapMutations } from 'vuex'
import promptdialog from '@/components/financial/promptdialog'
import {
  serviceCenterEmpInfo,
  auditMissingReportTip,
  auditPersonalDaily,
  auditCommitDailyPerson,
  auditOpen,
  switchQuery
} from '@/api/reimbursement'
import { dataChangeDay } from '@/utils/utils'
export default {
  name: 'personalcheck',
  components: {
    promptdialog
  },
  data () {
    return {
      onOff: false,
      cashMoney: '',
      cityCode: '',
      posMoney: '',
      userMessage: {
        empId: '',
        empName: '',
        nowDate: '',
        serviceCenterId: '',
        serviceCenterName: ''
      },
      day: '',
      list: [],
      remark: '',
      dialogShow: false,
      dialogText: {
        content: '个人报账金额存在差异，请确认',
        button: '确认结账',
        cancelBtn: '返回'
      },
      supplementShow: false,
      supplementText: {
        content: '此服务中心存在漏报账，请及时补报！',
        button: '去补报'
      },
      sureShow: false,
      sureShowText: {
        content: '',
        button: '确认结账',
        cancelBtn: '返回'
      },
      tableData: {
        money: {
          businessCode: '',
          platformCode: '',
          receiveAmount: 0,
          refundAmount: 0,
          type: '',
          remainAmount: 0
        },
        pos: {
          businessCode: '',
          platformCode: '',
          receiveAmount: 0,
          refundAmount: 0,
          type: '',
          remainAmount: 0
        }
      },
      spinShow: false,
      ifPay: false,
      isSuccess: false,
      tableList: [],
      totalData: {
        receiveAmountAdd: 0,
        posReceiveAmountAdd: 0,
        refundAmountAdd: 0,
        posRefundAmountAdd: 0,
        confirmAmountAdd: 0,
        receiptAmountAdd: 0,
        posReceiptReceiveAmountAdd: 0,
        posReceiptRefundAmountAdd: 0
      },
      flagOne: false,
      flagTwo: false,
      flagThree: false,
      times: 0,
      statisticsTime: null
    }
  },
  beforeMount () {
    this.auditMissingReportTip()
  },
  mounted () {
    if (this.$haveAuthority('roleRelativeData', '/SPLAT/auditFinance/switchQueryBtn')) {
      switchQuery({
        cityCode: this.userInfo.cityCode,
        empNo: this.userInfo.empNo,
        empName: this.userInfo.empName
      }).then(data => {
        this.onOff = Number(data) === 0
      }).catch(err => {
        this.$Message.error({ content: '服务中心报账开关查询错误:' + err.message, duration: 5 })
      })
    }
  },
  computed: {
    userInfo () {
      return this.$store.state.roleRelativeData.userInfo
    }
  },
  methods: {
    ...mapMutations([
      'closeTag'
    ]),
    handleBeforeChange () {
      return new Promise((resolve) => {
        this.$Modal.confirm({
          title: '切换确认',
          content: `您确认要${this.onOff ? '关闭' : '打开'}${this.userMessage.serviceCenterName}服务中心的报账开关吗？`,
          onOk: () => {
            auditOpen({
              cityCode: this.userInfo.cityCode,
              empNo: this.userInfo.empNo,
              auditOpen: !this.onOff ? 0 : 1
            }).then(() => {
              this.$Message.success('操作成功')
              resolve()
            }).catch(err => {
              this.$Message.error({ content: '操作失败:' + err.message, duration: 5 })
            })
          }
        })
      })
    },
    getMoney () {
      this.ifPay = true
      if (this.totalData.confirmAmountAdd === this.totalData.receiptAmountAdd && this.totalData.posReceiveAmountAdd === this.totalData.posReceiptReceiveAmountAdd && this.totalData.posRefundAmountAdd === this.totalData.posReceiptRefundAmountAdd) {
        // 满足“今日现金结账-实际现金小结”=“营业额现金小结”&“今日脱机POS结账-营业收款小结”=“今日脱机POS结账-实际收款小票合计”&“今日脱机POS结账-营业退款小结”=“今日脱机POS结账-营业退款合计”条件，可以结账
        this.flagOne = false
        this.flagTwo = false
        this.flagThree = false
        // 打开第二层确认次数弹窗
        this.setSureDialog()
      } else {
        // 不满足条件,标红字体，弹框
        this.flagOne = this.totalData.confirmAmountAdd !== this.totalData.receiptAmountAdd
        this.flagTwo = this.totalData.posReceiveAmountAdd !== this.totalData.posReceiptReceiveAmountAdd
        this.flagThree = this.totalData.posRefundAmountAdd !== this.totalData.posReceiptRefundAmountAdd
        // 打开第一层提示差异弹窗
        this.dialogShow = true
      }
    },
    setSureDialog () {
      // 确认次数弹窗展示
      this.sureShowText.content = '个人结账每日有3次机会，今日剩余' + this.times + '次'
      this.sureShowText.button = this.times === 0 ? '我知道了' : '确认结账'
      this.sureShowText.cancelBtn = this.times === 0 ? '' : '返回'
      this.sureShow = true
    },
    changeDialog (flag) {
      // 第一层弹窗确认，取消操作
      if (flag === true) {
        this.setSureDialog()
      } else {
        this.dialogShow = false
        if (!this.isSuccess) { // 如果是结账成功之后，关闭页面，按钮不允许点击
          this.ifPay = false
        }
      }
    },
    sureDialog (flag) {
      // 第二层弹窗确认，取消操作
      if (flag === true) {
        if (this.sureShowText.button === '我知道了') {
          this.sureShow = false
          this.dialogShow = false
          if (!this.isSuccess) { // 如果是结账成功之后，关闭页面，按钮不允许点击
            this.ifPay = false
          }
          return
        }
        this.auditCommitDailyPerson()
      } else {
        this.sureShow = false
        if (!this.dialogShow && !this.isSuccess) this.ifPay = false
      }
    },
    supplementDialog (flag) {
      if (flag === true) {
        alert('补报')
      } else {
        this.supplementShow = false
      }
    },
    serviceCenterEmpInfo () { // 获取用户信息
      serviceCenterEmpInfo({ cityCode: this.cityCode }).then((data) => {
        this.userMessage = data
        this.day = dataChangeDay(this.userMessage.nowDate)
        this.auditPersonalDaily()
      }).catch((err) => {
        this.$Message.warning({ content: '获取服务中心用户信息失败:' + err.message, duration: 5 })
      })
    },
    auditCommitDailyPerson () { // 结账
      let records = []
      let current = this.tableList[this.tableList.length - 1]
      let arr = [...this.tableList]
      if (this.tableList.length > 1) {
        records = arr.splice(0, this.tableList.length - 1)
      }
      auditCommitDailyPerson({
        cityCode: this.cityCode,
        queryDate: this.userMessage.nowDate,
        records: records,
        current: current,
        remark: this.remark,
        statisticsTime: this.statisticsTime
      }).then((data) => {
        this.dialogShow = false
        this.sureShow = false
        this.isSuccess = true // 增加一个参数，给一个结账成功的状态判断
        this.$Message.success({ content: '结账成功', duration: 5 })
        // let route = this.$route // 初始需求是结账之后，清空页面数据，前端逻辑实现是关闭之后，重新打开，后来的优化需求是结账成功之后，按钮不能再次点击
        // this.closeTag(route)
        // setTimeout(() => {
        //   this.$router.push('/financeMgt/personalcheck')
        // }, 100)
      }).catch((err) => {
        this.dialogShow = false
        this.sureShow = false
        this.$Message.warning({ content: '结账失败:' + err.message, duration: 5 })
        this.ifPay = false
      })
    },
    auditPersonalDaily () { // 个人收退费结账查询
      auditPersonalDaily({ cityCode: this.cityCode, queryDate: this.userMessage.nowDate }).then((data) => {
        this.tableList = data.records || []
        this.tableList.push(data.current)
        this.getTotal()
        let list = this.tableList
        if (list && list.length > 0) {
          // 处理输入框的小数点
          list.forEach((val, key) => {
            list[key].receiptAmount = list[key].receiptAmount.toFixed(2)
            list[key].posReceiptReceiveAmount = list[key].posReceiptReceiveAmount.toFixed(2)
            list[key].posReceiptRefundAmount = list[key].posReceiptRefundAmount.toFixed(2)
          })
        }
        this.times = data.leftCount
        this.statisticsTime = data.statisticsTime
        this.spinShow = false
      }).catch((err) => {
        this.spinShow = false
        this.$Message.warning({ content: '个人收退费结账查询失败:' + err.message, duration: 5 })
      })
    },
    auditMissingReportTip () {
      this.spinShow = true
      let interval = this.$store.state.app.missReportInterval
      if (interval) {
        clearInterval(interval)
      }
      auditMissingReportTip({
        isAudit: 1
      }).then(res => { // 判断是否有多个服务中心
        this.cityCode = res
        this.serviceCenterEmpInfo()
      }).catch((err) => {
        this.spinShow = false
        if (err.code === '40004') {
          this.$Modal.info({
            title: '提示',
            content: err.message,
            okText: '去补报',
            onOk: () => {
              let route = this.$route
              this.closeTag(route)
              this.$router.push({
                name: 'prescribeddaily'
              })
            }
          })
        } else {
          this.$Modal.info({
            title: '提示',
            content: err.message,
            okText: '确定',
            onOk: () => {
              let route = this.$route
              this.closeTag(route)
            }
          })
        }
      })
    },
    getTotal () {
      // 计算合计数据
      if (this.tableList.length > 0) {
        this.tableList.forEach((item, index) => {
          this.totalData.receiveAmountAdd += item.receiveAmount
          this.totalData.posReceiveAmountAdd += item.posReceiveAmount
          this.totalData.refundAmountAdd += item.refundAmount
          this.totalData.confirmAmountAdd += item.confirmAmount
          this.totalData.posRefundAmountAdd += item.posRefundAmount
          this.totalData.posReceiptReceiveAmountAdd += item.posReceiptReceiveAmount
          this.totalData.posReceiptRefundAmountAdd += item.posReceiptRefundAmount
          this.totalData.receiptAmountAdd += item.receiptAmount
        })
      }
    },
    changeData (type) {
      // 输入框改变时修改数据
      this.totalData[type + 'Add'] = 0
      this.tableList.forEach((item, index) => {
        this.totalData[type + 'Add'] += Number(item[type])
      })
    }
  }
}
</script>

