<template>
  <Modal
    :value="dialogShow"
    title="提示"
    width="300"
    @on-visible-change="cancel"
    :mask-closable="false">
    <p class="fin-text">{{dialogText ? dialogText.content : ''}}</p>
    <div slot="footer" class="fin-footer">
      <Button type="primary"  @click="ok">{{dialogText ? dialogText.button : '确认'}}</Button>
      <Button @click="cancel(false)" v-if="dialogText.cancelBtn" style="margin-left:20px;">{{dialogText ? dialogText.cancelBtn : '取消'}}</Button>
    </div>
  </Modal>
</template>

<script>
export default {
  name: 'promptdialog',
  data () {
    return {
      modal1: true
    }
  },
  props: ['dialogShow', 'dialogText'],
  methods: {
    ok () {
      this.$emit('changeDialog', true)
    },
    cancel (flag) {
      if (flag === false) {
        this.$emit('changeDialog', false)
      }
    }
  }
}
</script>

<style scoped>
.fin-text{
  text-align: center;
}
.fin-footer{
  display: flex;
  justify-content: center;
}
</style>
