<template>
  <Layout id="map">
    <TalIframe :content-src="dataSrc" />
  </Layout>
</template>

<script>
import { genManage } from '@/api/cashier'
import TalIframe from '@/components/iframe'
export default {
  name: 'Map',
  data () {
    return {
      dataSrc: ''
    }
  },
  components: { TalIframe },
  mounted () {
    genManage({
      cityCode: '010' // pos签到仅支持北京
    }).then(data => {
      this.dataSrc = data.url
    }).catch(err => {
      this.$Message.error({
        content: err.message,
        duration: 5
      })
    })
  }
}
</script>
