<script>
import {
  serviceCenterEmpInfo,
  auditCommitDailyServiceCenter,
  auditMissingReportTip,
  auditServiceCenterDaily
} from '@/api/reimbursement'
import promptdialog from '@/components/financial/promptdialog'
import { dataChangeDay } from '@/utils/utils'
import { mapMutations } from 'vuex'
export default {
  name: 'reimbursement',
  components: {
    promptdialog
  },
  data () {
    return {
      userMessage: {
        empId: '',
        empName: '',
        nowDate: '',
        serviceCenterId: '',
        serviceCenterName: ''
      },
      list: [],
      remark: '',
      dialogShow: false,
      dialogText: {
        content: '服务中心现金营业款报账金额存在差异，请确认！',
        button: '确认报账',
        cancelBtn: '返回'
      },
      day: '',
      exisList: [
        {
          depositCode: null,
          purpose: '',
          depositAmount: null,
          depositType: null,
          costControlSystemNo: null
        }
      ],
      cityCode: null,
      auditDaily: {
        operateList: [],
        businessPaymentBeginBalance: 0,
        businessReceiptTodayAmount: 0,
        businessRefundTodayAmount: 0,
        tempReserveBeginBalance: 0,
        quotaReserveBeginBalance: 0,
        cashSafeYesterdayAmount: 0,
        otherBeginBalance: 0
      },
      tableTotalData: [],
      servcenterOtherBudget: {
        otherIncomeBookAmount: null,
        otherIncomeRegistFeeAmount: null,
        otherIncomeAmount: null,
        otherIncomeSumAmount: 0,
        otherExpenseBookAmount: null,
        otherExpenseRegistFeeAmount: null,
        otherExpenseAmount: null,
        otherExpenseSumAmount: 0,
        difference: 0
      },
      serventerDayAudit: {
        tempReserveLoanTodayAmount: null, // 临时备用金借款
        cashSafeYesterdayAmount: null, // 昨日保险柜实际现金金额-营业款、备用金
        quotaReserveLoanTodayAmount: null, // 长期备用金借款
        quotaReserveRepaymentTodayAmount: 0, // 长期备用金还款
        tempReserveRepaymentTodayAmount: 0, // 临时备用金还款
        cashStoreTodyAmount: null,
        cashActualBalanceTodyAmount: null, // 今日现金账面实际余额-营业款、备用金
        crossCampusAdjustAmount: null, // 跨校区调整金额
        lengthShortnessAmount: null,
        cashBalanceTodyAmount: null,
        otherIncomeAndExpenditureYesterday: null, // 昨日现金其他收支
        otherDepositCash: 0, // 书籍费、考试报名费其他存现
        otherIncomeAndExpenditureToday: null,
        businessPaymentBeginBalance: null,
        businessReceiptTodayAmount: null,
        businessRefundTodayAmount: null,
        businessPaymentEndBalance: null,
        tempReserveBeginBalance: null,
        quotaReserveBeginBalance: null,
        quotaReserveEndBalance: null,
        otherBeginBalance: null,
        otherNetIncomeAndExpenditure: null,
        otherEndBalance: null
      },
      accountType: 1,
      cashStoreTodyAmount: 0,
      reimLoading: false,
      depositTypeList: [
        {
          id: 1,
          name: '营业款'
        },
        {
          id: 2,
          name: '临时备用金'
        },
        {
          id: 3,
          name: '长期备用金'
        },
        {
          id: 4,
          name: '其他收支'
        }
      ],
      servcenterDepositList: []
    }
  },
  beforeMount () {
    this.auditMissingReportTip()
    if (this.$route.query.type === 'accountType') { // 是补报账
      this.accountType = 2
    }
  },
  watch: {
    exisList: {
      handler (newVal, oldVal) {
        let number = 0
        let number2 = 0
        let number3 = 0
        let number4 = 0
        this.exisList.forEach((v, k) => {
          if (v.depositType === 1) {
            number += Number(v.depositAmount)
          } else if (v.depositType === 2) {
            number2 += Number(v.depositAmount)
          } else if (v.depositType === 3) {
            number3 += Number(v.depositAmount)
          } else if (v.depositType === 4) {
            number4 += Number(v.depositAmount)
          }
        })
        this.cashStoreTodyAmount = number
        this.serventerDayAudit.tempReserveRepaymentTodayAmount = number2
        this.serventerDayAudit.quotaReserveRepaymentTodayAmount = number3
        this.serventerDayAudit.otherDepositCash = number4
      },
      deep: true
    }
  },
  computed: {
    addUpOne () {
      // 其他收入合计
      return (Number(this.servcenterOtherBudget.otherIncomeBookAmount) + Number(this.servcenterOtherBudget.otherIncomeRegistFeeAmount) + Number(this.servcenterOtherBudget.otherIncomeAmount)).toFixed(2)
    },
    addUpTwo () {
      // 其他支出合计
      return (Number(this.servcenterOtherBudget.otherExpenseBookAmount) + Number(this.servcenterOtherBudget.otherExpenseRegistFeeAmount) + Number(this.servcenterOtherBudget.otherExpenseAmount)).toFixed(2)
    },
    addUpThree () {
      // 净额
      return ((Number(this.servcenterOtherBudget.otherIncomeBookAmount) + Number(this.servcenterOtherBudget.otherIncomeRegistFeeAmount) + Number(this.servcenterOtherBudget.otherIncomeAmount)) - (Number(this.servcenterOtherBudget.otherExpenseBookAmount) + Number(this.servcenterOtherBudget.otherExpenseRegistFeeAmount) + Number(this.servcenterOtherBudget.otherExpenseAmount))).toFixed(2)
    },
    businessPaymentEndBalance () {
      // 营业款-期末余额
      return (Number(this.auditDaily.businessPaymentBeginBalance) + Number(this.auditDaily.businessReceiptTodayAmount) - Number(this.auditDaily.businessRefundTodayAmount) - Number(this.cashStoreTodyAmount) + Number(this.serventerDayAudit.crossCampusAdjustAmount)).toFixed(2)
    },
    tempReserveEndBalance () {
      // 临时备用金期末余额
      return (Number(this.auditDaily.tempReserveBeginBalance) + Number(this.serventerDayAudit.tempReserveLoanTodayAmount) - Number(this.serventerDayAudit.tempReserveRepaymentTodayAmount)).toFixed(2)
    },
    quotaReserveEndBalance () {
      // 长期备用金期末余额
      return (Number(this.auditDaily.quotaReserveBeginBalance) + Number(this.serventerDayAudit.quotaReserveLoanTodayAmount) - Number(this.serventerDayAudit.quotaReserveRepaymentTodayAmount)).toFixed(2)
    },
    cashBalanceTodyAmount () {
      // 今日现金账面余额总计
      return (Number(this.businessPaymentEndBalance) + Number(this.tempReserveEndBalance) + Number(this.quotaReserveEndBalance)).toFixed(2)
    },
    lengthShortnessAmount () {
      // 长短款
      return (Number(this.cashBalanceTodyAmount) - Number(this.serventerDayAudit.cashActualBalanceTodyAmount)).toFixed(2)
    },
    otherEndBalance () {
      // 书籍费、考试报名费其他期末余额
      return (Number(this.auditDaily.otherBeginBalance) + Number(this.addUpThree) - Number(this.serventerDayAudit.otherDepositCash)).toFixed(2)
    }
  },
  methods: {
    ...mapMutations([
      'closeTag'
    ]),
    getTotalData () {
      // 获取数据之后进行处理
      let obj1 = {
        operateName: '合计'
      }
      let obj2 = {
        operateName: '款项所属服务中心合计'
      }
      let obj3 = {
        operateName: '合计差额'
      }
      if (this.auditDaily.operateList && this.auditDaily.operateList.length > 0) {
        this.auditDaily.operateList.forEach((val, key) => {
          if (val.operateId === '999999') {
            // 款项所属服务中心合计为后台返回
            obj2 = Object.assign(val, obj2)
          } else {
            // 累加合计数据
            for (var i in val) {
              obj1[i] = obj1[i] ? obj1[i] : 0
              if (i !== 'operateName') {
                obj1[i] += val[i]
              }
            }
          }
        })
        // 合计差额为款项所属服务中心合计-合计
        for (var i in obj1) {
          if (i !== 'operateName') {
            obj3[i] = (obj2[i] ? obj2[i] : 0) - (obj1[i] ? obj1[i] : 0)
          }
        }
        obj2.receiptAmount = obj3.receiptAmount = null // 实际现金小结合计差额为--
        obj2.posReceiptReceiveAmount = obj3.posReceiptReceiveAmount = null // 实际现金小结合计差额为--
        obj2.posReceiptRefundAmount = obj3.posReceiptRefundAmount = null // 实际现金小结合计差额为--
        this.tableTotalData = [obj1, obj2, obj3]
      }
    },
    getMoney () {
      // 页面中的报账按钮
      this.servcenterDepositList = []
      for (var i = 0; i < this.exisList.length; i++) {
        let str = ''
        let v = this.exisList[i]
        // 校验必填项
        if (!v.depositType && (v.depositCode || v.purpose || v.depositAmount)) {
          this.$Message.warning({ content: '请选择存款类型！', duration: 5 })
          return
        }
        if ((v.depositType === 2 || v.depositType === 3) && !v.costControlSystemNo) {
          this.$Message.warning({ content: '请填写费控系统单据编号！', duration: 5 })
          return
        }
        if (v.depositType && !v.depositAmount) {
          this.$Message.warning({ content: '请填写存款金额！', duration: 5 })
          return
        }
        // 不是必填项，都为空时，传数组，一个输入框不为空，则进行校验
        if (!v.depositCode && !v.purpose && v.depositAmount) {
          this.$Message.warning({ content: '请填写存款码或用途！', duration: 5 })
          return
        }
        if ((v.depositCode || v.purpose) && !v.depositAmount) {
          this.$Message.warning({ content: '请填写存款金额！', duration: 5 })
          return
        }
        for (var j in v) {
          if (v[j]) {
            str = 'has'
          }
        }
        if (str) {
          this.servcenterDepositList.push(v)
        }
      }
      this.auditCommitDailyServiceCenter(1)
    },
    changeDialog (flag) {
      if (flag === true) {
        this.auditCommitDailyServiceCenter()
      } else {
        this.dialogShow = false
      }
    },
    addExis () {
      let obj = {
        depositCode: null,
        purpose: '',
        depositAmount: null,
        depositType: null,
        costControlSystemNo: null
      }
      this.exisList.push(obj)
    },
    delExis (k) {
      this.exisList.splice(k, 1)
    },
    serviceCenterEmpInfo () { // 获取用户信息
      serviceCenterEmpInfo({
        cityCode: this.cityCode
      }).then((data) => {
        this.userMessage = data
        if (this.$route.query.time) {
          this.userMessage.nowDate = this.$route.query.time
        }
        this.day = dataChangeDay(this.userMessage.nowDate)
        this.auditServiceCenterDaily()
      }).catch((err) => {
        this.$Message.warning({ content: '获取服务中心用户信息失败1:' + err.message, duration: 5 })
      })
    },
    auditCommitDailyServiceCenter (flag) { // 结账
      // 其他收支
      this.servcenterOtherBudget.otherIncomeSumAmount = Number(this.addUpOne)
      this.servcenterOtherBudget.otherExpenseSumAmount = Number(this.addUpTwo)
      this.servcenterOtherBudget.difference = Number(this.addUpThree)
      // 营业款、备用金合计
      this.serventerDayAudit.businessPaymentBeginBalance = this.auditDaily.businessPaymentBeginBalance // 营业款期初余额
      this.serventerDayAudit.businessReceiptTodayAmount = this.auditDaily.businessReceiptTodayAmount // 本日营业款收款
      this.serventerDayAudit.businessRefundTodayAmount = this.auditDaily.businessRefundTodayAmount // 本日营业款退款
      this.serventerDayAudit.cashStoreTodyAmount = Number(this.cashStoreTodyAmount) // 营业款存现
      this.serventerDayAudit.businessPaymentEndBalance = Number(this.businessPaymentEndBalance) // 营业款期末余额
      this.serventerDayAudit.tempReserveBeginBalance = this.auditDaily.tempReserveBeginBalance // 临时备用金期初余额
      this.serventerDayAudit.tempReserveEndBalance = Number(this.tempReserveEndBalance) // 临时备用金期末余额
      this.serventerDayAudit.quotaReserveBeginBalance = this.auditDaily.quotaReserveBeginBalance // 长期备用金期初余额
      this.serventerDayAudit.quotaReserveEndBalance = Number(this.quotaReserveEndBalance) // 长期备用金期末余额
      this.serventerDayAudit.cashBalanceTodyAmount = Number(this.cashBalanceTodyAmount) // 今日现金账面余额总计
      // 其他收支合计
      this.serventerDayAudit.cashSafeYesterdayAmount = this.auditDaily.cashSafeYesterdayAmount // 昨日保险柜实际现金金额-营业款、备用金
      this.serventerDayAudit.lengthShortnessAmount = Number(this.lengthShortnessAmount) // 长短款
      this.serventerDayAudit.otherBeginBalance = this.auditDaily.otherBeginBalance // 书籍费、考试报名费其他期初余额
      this.serventerDayAudit.otherNetIncomeAndExpenditure = Number(this.addUpThree) // 书籍费、考试报名费其他收支净额
      this.serventerDayAudit.otherEndBalance = Number(this.otherEndBalance) // 书籍费、考试报名费其他期末余额
      if (this.serventerDayAudit.lengthShortnessAmount && this.serventerDayAudit.lengthShortnessAmount !== 0 && flag) { // 长短款有差异
        this.dialogShow = true
        return
      }
      let servcenterOtherBudget = {}
      let serventerDayAudit = {}
      // this.exisList.forEach((v, k) => { // 判断营业款银行存现为空时传空字符串
      //   let str = ''
      //   for (var i in v) {
      //     if (v[i]) {
      //       str = 'has'
      //     }
      //   }
      //   // 校验必填项
      //   if (!v.depositType && (v.depositCode || v.purpose || v.depositAmount)) {
      //     this.$Message.warning({ content: '请填写存款类型！', duration: 5 })
      //     emt = 'has'
      //   }
      //   if (v.depositType === 2 && !v.costControlSystemNo) {
      //     this.$Message.warning({ content: '请填写费控系统单据编号！', duration: 5 })
      //     emt = 'has'
      //   }
      //   // 不是必填项，都为空时，传数组，一个输入框不为空，则进行校验
      //   if (!v.depositCode && !v.purpose && v.depositAmount) {
      //     this.$Message.warning({ content: '请填写存款码或用途！', duration: 5 })
      //     emt = 'has'
      //   }
      //   if ((v.depositCode || v.purpose) && !v.depositAmount) {
      //     this.$Message.warning({ content: '请填写存款金额！', duration: 5 })
      //     emt = 'has'
      //   }
      //   if (str) {
      //     servcenterDepositList.push(v)
      //   }
      // })
      for (let i in this.servcenterOtherBudget) {
        servcenterOtherBudget[i] = this.servcenterOtherBudget[i] || 0
      }
      for (let i in this.serventerDayAudit) {
        serventerDayAudit[i] = this.serventerDayAudit[i] || 0
      }
      let operateList = [...this.auditDaily.operateList]
      operateList.forEach((val, key) => {
        // 提交数据时，再转为0
        if (val.operateId === '999999') {
          operateList[key].receiptAmount = 0
          operateList[key].posReceiptReceiveAmount = 0
          operateList[key].posReceiptRefundAmount = 0
        }
      })
      let params = {
        servcenterDepositList: this.servcenterDepositList,
        servcenterOtherBudget: servcenterOtherBudget,
        serventerDayAudit: serventerDayAudit,
        operateList: operateList
      }
      if (this.accountType === 2) {
        this.$Modal.confirm({
          title: '提示',
          content: '服务中心补报只有1次机会，请确认数据无误！',
          onOk: () => {
            params.checkEmp = 1 // 不需要校验员工结账情况
            this.goRecon(params)
          },
          onCancel: () => {
            this.dialogShow = false
          }
        })
      } else if (this.accountType === 1) {
        params.checkEmp = 0 // 需要校验员工结账情况
        this.goRecon(params)
      }
    },
    goRecon (params) {
      // 请求报账接口
      auditCommitDailyServiceCenter({
        queryDate: this.userMessage.nowDate,
        cityCode: this.cityCode,
        accountType: this.accountType,
        createrName: this.userMessage.empName,
        remarks: this.remark,
        serviceCenterName: this.userMessage.serviceCenterName,
        serviceCenterId: this.userMessage.serviceCenterId,
        operateList: params.operateList,
        servcenterDepositList: params.servcenterDepositList,
        servcenterOtherBudget: params.servcenterOtherBudget,
        serventerDayAudit: params.serventerDayAudit,
        checkEmp: params.checkEmp,
        statisticsTime: this.auditDaily.statisticsTime
      }).then((data) => {
        this.$Message.success({ content: '报账成功', duration: 5 })
        this.dialogShow = false
        let route = this.$route
        this.closeTag(route)
        setTimeout(() => {
          this.$router.push({
            name: 'reimbursement'
          })
        }, 100)
      }).catch((err) => {
        if (err.code === '30005') {
          // 已经校验了员工结账情况
          this.$Modal.confirm({
            title: '提示',
            content: err.message,
            onOk: () => {
              params.checkEmp = 1
              this.dialogShow = false
              this.goRecon(params)
            },
            onCancel: () => {
              this.dialogShow = false
            }
          })
        } else {
          this.$Message.warning({ content: '报账失败:' + err.message, duration: 5 })
          this.dialogShow = false
        }
      })
    },
    auditServiceCenterDaily () { // 获取营业款信息
      this.reimLoading = true
      auditServiceCenterDaily({
        queryDate: this.userMessage.nowDate,
        cityCode: this.cityCode
      }).then((data) => {
        this.auditDaily = data
        this.serventerDayAudit.otherIncomeAndExpenditureYesterday = data.otherIncomeAndExpenditureYesterday.toFixed(2)
        let obj = { // 数据从接口获取，并且可修改，转为2位小数
          businessPaymentBeginBalance: null,
          tempReserveBeginBalance: null,
          quotaReserveBeginBalance: null,
          otherBeginBalance: null
        }
        for (var i in obj) {
          this.auditDaily[i] = this.auditDaily[i].toFixed(2)
        }
        // this.auditDaily.businessPaymentBeginBalance = this.auditDaily.businessPaymentBeginBalance.toFixed(2)
        this.getTotalData()
      }).catch((err) => {
        this.$Message.warning({ content: '获取服务中心报账信息失败:' + err.message, duration: 5 })
      }).finally(() => {
        this.reimLoading = false
      })
    },
    auditMissingReportTip () {
      let interval = this.$store.state.app.missReportInterval
      if (interval) {
        clearInterval(interval)
      }
      auditMissingReportTip({
        isAudit: 1,
        queryDate: this.$route.query.time
      }).then(res => {
        this.cityCode = res
        this.serviceCenterEmpInfo()
      }).catch((err) => {
        if (err.code === '40004') {
          this.$Modal.info({
            title: '提示',
            content: err.message,
            okText: '去补报',
            onOk: () => {
              let route = this.$route
              this.closeTag(route)
              this.$router.push({
                name: 'prescribeddaily'
              })
            }
          })
        } else {
          this.$Modal.info({
            title: '提示',
            content: err.message,
            okText: '确定',
            onOk: () => {
              let route = this.$route
              this.closeTag(route)
            }
          })
        }
      })
    },
    setFloat (v, k) { // 处理小数点，小数点显示两位
      let num = v.depositAmount.match(/\d+(\.\d{0,2})?/) ? v.depositAmount.match(/\d+(\.\d{0,2})?/)[0] : ''
      this.$set(this.exisList[k], 'depositAmount', num)
      if (v.depositType === 1) {
        // 最大值不能超过“营业款期初余额+本日营业收款-本日营业退款”值
        if (Number(this.businessPaymentEndBalance) < 0) {
          this.$Message.warning('营业款存现值超出今日营业款余额最大值，请核对后再输入！')
        }
      }
    },
    setLength (v, k) {
      // 设置可输入的最长长度
      if (v.depositAmount.length > 30) {
        let num = v.depositAmount.slice(0, 30)
        this.$set(this.exisList[k], 'depositAmount', num)
      }
    },
    dealDataFormat (data, type) {
      // 只允许输入2位小数正数
      this[data][type] = this[data][type].match(/\d+(\.\d{0,2})?/) ? this[data][type].match(/\d+(\.\d{0,2})?/)[0] : ''
    },
    dealDataFormatFloat (data, type) {
      // 只允许输入2位小数，可以为负数
      this[data][type] = this[data][type].match(/^(-|\+)?\d+(\.\d{0,2})?/) ? this[data][type].match(/^(-|\+)?\d+(\.\d{0,2})?/)[0] : ''
    },
    changeDepositType (k) {
      this.exisList[k].costControlSystemNo = null
    }
  }
}
</script>

